.who-we-are {
  background-color: #e2edff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7vh 7vw;
}

.who-we-are h2 {
  font-weight: 900;
  color: #315ea9;
  font-size: 2.5rem;
  margin-bottom: 5vh;
}

.who-we-are p {
  font-weight: 400;
  color: #083888;
  width: 60%;
  text-align: center;
}

.who-we-are a {
  text-decoration: none;
  border: 2px solid #083888;
  color: #083888;
  padding: 14px 30px;
  border-radius: 15px;
  font-weight: 600;
  margin-top: 5vh;
  transition: 0.3s;
}

.who-we-are a:hover {
  background-color: rgba(8, 56, 136, 0.8);
  color: white;
}
